
import FormPreview from 'vux/src/components/form-preview/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
Vue.use(AlertPlugin)

var test = "test";

export default{
  components: {
    FormPreview
  },
  data(){
    return{
      orderList:[
        {
          id:123,
          price:"¥2400.00",
          fields:[
            {
              label: '订单编号',
              value: '1485419079408'
            },
            {
              label: '用户手机号码',
              value: '名字名字名字'
            },
            {
              label: '商品名称',
              value: '很长很长的名字很长很长的名字很长很长的名字很长很长的名字很长很长的名字'
            }
          ],
          status:"WAIT_FOR_PAY"
        },
        {
          id:456,
          price:"¥2300.00",
          fields:[
            {
              label: '订单编号',
              value: '1485419079408'
            },
            {
              label: '用户手机号码',
              value: '名字名字名字'
            }, {
              label: '商品名称',
              value: '很长很长的名字很长很长的名字很长很长的名字很长很长的名字很长很长的名字'
            }
          ],
          status:"PAY_SUCCESS"
        }
      ]
    }
  },
  mounted:function(){
    var orderList = this.orderList;
    var vux = this.$vux;
    var httpClient = this.$http;
    this.$http.get('/orders/all').then(
      function(response){
        if("0" == response.resultCode){
          response.items.forEach(function(item){
            var goodsTitle;
            httpClient.get("/goods/"+this.goodsId).then(
              function(response){
                goodsTitle = response.title;
              },
              function(){
                vux.alert.show({
                  title: '提示',
                  content: '获取订单失败！',
                  onHide () {
                      window.location.reload();
                  }
                });
              }
            );

            var userPhoneNumber;
            httpClient.get("/user/"+this.userId).then(
              function(response){
                userPhoneNumber = response.phoneNumber;
              },
              function(){
                vux.alert.show({
                  title: '提示',
                  content: '获取订单失败！',
                  onHide () {
                      window.location.reload();
                  }
                });
              }
            );

            order.fields = [
              {
                label: '订单编号',
                value: this.displayId
              },
              {
                label: '用户手机号码',
                value: userPhoneNumber
              }, {
                label: '商品名称',
                value: goodsTitle
              }
            ];

            order.price = "¥" + this.price + ".00";

            orderList.push(order);
          })
        }else{
          vux.alert.show({
            title: '提示',
            content: '获取订单失败！',
            onHide () {
                window.history.back();
            }
          });
        }
      },
      function(){
        vux.alert.show({
          title: '提示',
          content: '获取订单失败！',
          onHide () {
              window.history.back();
          }
        });

      }
    )
  },
  methods:{
    getButton(order){
      var button = {
        style: 'default',
        text:"订单已完成",
        onButtonClick: function() {
        }
      }

      var httpClient = this.$http;
      var vux = this.$vux;
      if("WAIT_FOR_PAY" == order.status){
        button.style = "primary";
        button.text = "点击确认收到用户付款";

        button.onButtonClick = function () {
          httpClient.post('/orders/action/userPayed/orderId/'+order.id,{}).then(
            function(response){
               if("0" == response.resultCode){
                 vux.alert.show({
                   title: '提示',
                   content: '操作成功！',
                   onHide () {
                      window.location.reload();
                   }
                 });
               }else{
                 vux.alert.show({
                   title: '提示',
                   content: '操作失败！'
                 });
               }
            },
            function(){
               vux.alert.show({
                 title: '提示',
                 content: '操作失败！'
               });
            }
          )
        }
      }
      if("PAY_SUCCESS" == order.status){
        button.style = "primary";
        button.text = "点击发货";
        button.onButtonClick =function() {
          button.onButtonClick = function () {
            httpClient.post('/orders/action/deliverGoods/orderId/'+order.id,{}).then(
              function(response){
                 if("0" == response.resultCode){
                   vux.alert.show({
                     title: '提示',
                     content: '操作成功！',
                     onHide () {
                        window.location.reload();
                     }
                   });
                 }else{
                   vux.alert.show({
                     title: '提示',
                     content: '操作失败！'
                   });
                 }
              },
              function(){
                 vux.alert.show({
                   title: '提示',
                   content: '操作失败！'
                 });
              }
            )
          }
        }
      }
      if("DILIVERED" == order.status){
        button.style = "primary";
        button.text = "点击确认用户收到商品";
        button.onButtonClick =function() {
          button.onButtonClick = function () {
            httpClient.post('/orders/action/receiveGoods/orderId/'+order.id,{}).then(
              function(response){
                 if("0" == response.resultCode){
                   vux.alert.show({
                     title: '提示',
                     content: '操作成功！',
                     onHide () {
                        window.location.reload();
                     }
                   });
                 }else{
                   vux.alert.show({
                     title: '提示',
                     content: '操作失败！'
                   });
                 }
              },
              function(){
                 vux.alert.show({
                   title: '提示',
                   content: '操作失败！'
                 });
              }
            )
          }
        }
      }
      return [button]
    }
  }
}



